import date from '@/common/date'
export default {
	data() {
		return {
			distance: -740,
			skimList: [
			],
			notice: [
			],
			noWinner: true
		};
	},
	onLoad() {
		this.getSkims()
		this.getNotice()
	},
	methods: {
		goPage(id) {
			uni.navigateTo({
				url: './draw-detail?data=' + JSON.stringify(this.skimList[id])
			})
		},
		headerRoll() {
			if (this.distance< -400) {
				this.distance = 710
			}
			this.distance = this.distance - 3
			setTimeout(()=>{
				this.headerRoll()
			},40)
		},
		getNotice() {
			this.doRequest('/winner/get').then(res=>{
				if (res.data.length >1) {
					this.notice = res.data
					this.noWinner = false
					this.scroll()
				} else if (res.data.length == 0) {
					this.noWinner = true
					this.headerRoll()
				}
			})
		},
 		scroll(){
		    setTimeout(()=>{
			    this.notice.push(this.notice[0]);
			    this.notice.shift();
				this.scroll()
		    },2000)
		},
		getSkims() {
			this.doRequest('/skim/getSkims').then(res=>{
				let data = res.data.map(i=>{
					i.closeDate = date(i.closeDate)
					return i
				})
				this.skimList = data
			})
		},
		
	},
	onShareAppMessage(res) {
		uni.showToast({
			title: 'share'
		})
	    return {
	      title: '王者荣耀，免费抽皮肤，和好兄弟开黑...',
	      path: '/pages/tabbar/login/login?parentID='+ this.$store.state.uuid
	    }
	}
};
